@use "sass:math";

// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1080px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 15px;

////////////////////
// フォント設定
////////////////////

$base-font-family: "Noto Sans JP", sans-serif;
$english-font-family:"Gotu", sans-serif;
$number-font-family: "Lato", sans-serif;
// ※検証ツールでしっかり反映されているか確認する

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$main-green:#408F95;
$font-color:#0D2936;
$red:#C94800;
$bg-green:#DDF0F1;
$white:#fff;


body {
  font-family:$base-font-family;
  color:$font-color;
}

// フォントウェイト
$regular: 400;
$medium:500;
$bold: 700;

